
.event-card {
    margin: 1rem 1rem;
    padding: 0.75rem 1rem;
    height : 20rem;
    border: 3px solid black;
    background-color: black;
    border-radius: 10%;
    transition: all 1s ease;
    position:relative;
    width: auto;
}


.event-card:hover {
    z-index: 20;
    transform: scale(1.3);
}

.event-image {
    width : 100%;
    height: 60%;
    border-radius: 10%;
    background-image: none;
    background-repeat: no-repeat;
    background-size: cover;
}

.event-name {
    font-size: 25px;
    align-items: left;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 20px;
    text-align: left;
    font-variant: small-caps
}

.event-button {
    display: flex;
    width: 10rem;
    height: 2rem;
    text-align: center;
    border-radius: 20px;
    border: 1px solid black;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    font-family: Arial, Helvetica, sans-serif;
    font-variant: small-caps;
    font-weight: 300;

}

span {
    text-align: center;

}

.event-date-container {
    border-radius: 20px 0px;
    background-color: rgb(0, 0, 0);
    box-sizing: border-box !important;
    text-align: center;
    outline: none !important;
    min-width: 100px;
    min-height: 30px;
    padding: 10px;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
    display: flex;
    *flex-direction: row-reverse;
    justify-content: center;
    align-items: stretch;
    flex-shrink: 0;
    align-self: stretch;
}

.event-date {
    font-size: 17px;
    font-family: Arial, Helvetica, sans-serif;
    text-transform: uppercase;
}

.event-location {
    font-size: 15px;
    align-items: left;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 3px;
    text-align: left;
    font-variant: small-caps;
    font-weight: 50;
}
